'use client'

import { useState, useEffect, Suspense } from 'react'
import { useRouter, useSearchParams } from 'next/navigation'
import { useAuth } from '@/lib/auth-context'
import { Pet } from '@/lib/types'
import { mockPets } from '@/lib/mock-data'
import { InteractiveMap } from '@/components/interactive-map'
import { MapPetCard } from '@/components/map-pet-card'
import { SightingDialog } from '@/components/sighting-dialog'
import { MobileNav } from '@/components/mobile-nav'
import { Button } from '@/components/ui/button'
import { ArrowLeft } from 'lucide-react'

function MapContent() {
  const { user, isLoading } = useAuth()
  const router = useRouter()
  const searchParams = useSearchParams()
  const petIdFromUrl = searchParams.get('petId')

  const [pets] = useState<Pet[]>(mockPets)
  const [selectedPet, setSelectedPet] = useState<Pet | null>(null)
  const [selectedPetForSighting, setSelectedPetForSighting] = useState<Pet | null>(null)
  const [statusFilter, setStatusFilter] = useState<'all' | 'lost' | 'found' | 'adoption'>('all')

  useEffect(() => {
    if (!isLoading && !user) {
      router.push('/login')
    }
  }, [user, isLoading, router])

  useEffect(() => {
    if (petIdFromUrl) {
      const pet = pets.find((p) => p.id === petIdFromUrl)
      if (pet) {
        setSelectedPet(pet)
      }
    }
  }, [petIdFromUrl, pets])

  const handleSubmitSighting = (sightingData: any) => {
    alert('Avistamento reportado com sucesso!')
  }

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <div className="w-16 h-16 border-4 border-primary border-t-transparent rounded-full animate-spin mx-auto mb-4" />
          <p className="text-muted-foreground">Carregando...</p>
        </div>
      </div>
    )
  }

  if (!user) {
    return null
  }

  return (
    <div className="h-screen flex flex-col">
      <header className="bg-white border-b z-50 shrink-0 shadow-sm">
        <div className="container mx-auto px-3 py-3 sm:px-4 flex items-center gap-3">
          <Button
            variant="ghost"
            size="default"
            onClick={() => router.push('/')}
            className="p-2.5 h-auto"
          >
            <ArrowLeft className="w-6 h-6 sm:w-5 sm:h-5" />
          </Button>
          <div className="flex items-center gap-2">
            <div className="w-9 h-9 sm:w-8 sm:h-8 bg-teal-600 rounded-full flex items-center justify-center">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                strokeWidth={2}
                stroke="currentColor"
                className="w-6 h-6 sm:w-5 sm:h-5 text-white"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M21 8.25c0-2.485-2.099-4.5-4.688-4.5-1.935 0-3.597 1.126-4.312 2.733-.715-1.607-2.377-2.733-4.313-2.733C5.1 3.75 3 5.765 3 8.25c0 7.22 9 12 9 12s9-4.78 9-12z"
                />
              </svg>
            </div>
            <h1 className="text-lg sm:text-xl font-bold">Mapa de Pets</h1>
            <div className="ml-3 flex items-center gap-2">
              <Button size="sm" variant={statusFilter === 'all' ? 'default' : 'outline'} onClick={() => setStatusFilter('all')}>Todos</Button>
              <Button size="sm" variant={statusFilter === 'lost' ? 'default' : 'outline'} onClick={() => setStatusFilter('lost')}>Perdidos</Button>
              <Button size="sm" variant={statusFilter === 'found' ? 'default' : 'outline'} onClick={() => setStatusFilter('found')}>Encontrados</Button>
              <Button size="sm" variant={statusFilter === 'adoption' ? 'default' : 'outline'} onClick={() => setStatusFilter('adoption')}>Adoção</Button>
            </div>
          </div>
        </div>
      </header>

      <div className="flex-1 flex flex-col overflow-hidden pb-20 sm:pb-16">
        <div className={`flex-1 ${selectedPet ? 'h-[48%] sm:h-1/3' : 'h-[65vh] sm:h-[60vh]'} transition-all`}>
          <InteractiveMap
            pets={pets}
            selectedPetId={selectedPet?.id}
            onPetSelect={setSelectedPet}
            statusFilter={statusFilter}
          />
        </div>

        {selectedPet && (
          <div className="bg-white border-t p-3.5 sm:p-4 overflow-y-auto shadow-lg" style={{ maxHeight: '45%' }}>
            <MapPetCard
              pet={selectedPet}
              onReportSighting={setSelectedPetForSighting}
            />
          </div>
        )}
      </div>

      <SightingDialog
        pet={selectedPetForSighting}
        open={!!selectedPetForSighting}
        onClose={() => setSelectedPetForSighting(null)}
        onSubmit={handleSubmitSighting}
      />

      <MobileNav />
    </div>
  )
}

export default function MapPage() {
  return (
    <Suspense fallback={
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <div className="w-16 h-16 border-4 border-primary border-t-transparent rounded-full animate-spin mx-auto mb-4" />
          <p className="text-muted-foreground">Carregando...</p>
        </div>
      </div>
    }>
      <MapContent />
    </Suspense>
  )
}
